<template>
  <h1>Check Your Reaction Time </h1>
  <button @click="start" :class="{ disabled:isPlaying}"> {{ setText }} </button>
  <p v-if="showResults">Reaction TIme {{ score }}ms
         <br>   <br>
         <span class="rank">{{ rankName }}</span>
  </p>
   <Block v-if="isPlaying" :delay="delay" @end="endgame" />
</template>

<script>
import  Block from "./components/Block"
export default {
  name: 'App',
  data(){
    return {
      isPlaying: false,
      delay: null,
      score:null,
      showResults: false,
      setText: "Play",
      rank: null,
      rankName: null
    }
  },
  components: {
    Block
  },
  methods: {
    start(){
       this.delay = 2000 + Math.random() * 3000
        this.isPlaying = true
        console.log(this.delay)
        this.showResults = false
    },
    endgame(reactionTime){
        this.score = reactionTime
        this.isPlaying = false
        this.showResults = true
        this.setText = "Play Again"
        if(this.score < 250) {
            this.rankName = 'Ninja Fingers'
        }else if(this.score < 700){
            this.rankName = 'Rapid Reflex'
        }else{
             this.rankName = 'Snail pace....'
        }
    }
  }
}
</script>

