<template>
  <div v-if="showBlock" class="block"  @click="stopTimer">
      Click me
  </div>
</template>

<script>
export default {
   props: ["delay"],
   data(){
        return {
            showBlock : false,
            timer : null,
            reactionTime: 0
        }
   },
   mounted(){
       //console.log("Compoment mounted ")
       setTimeout(()=>{
           this.showBlock = true
           this.startTimer()
       }, this.delay)
   },
   methods:{
       startTimer(){
           this.timer = setInterval(()=>{
             this.reactionTime += 10
           },10)
       },
       stopTimer(){
           clearInterval(this.timer)
         //  console.log(this.reactionTime)
          this.$emit("end", this.reactionTime)
       }
   }
}
</script>

<style>
.block{
    background-color: rgb(1, 151, 151);
    width:   50%;
    font-size:2em;
    margin: 1.5em 25%;
    color: whitesmoke;
    border-radius: 0.5em;
    padding: 20px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
</style>